.item{
    width: 280px;
}

.item img{
    width: 280px;
}

.item p{
    margin: 6px 0px;
}

.item-price{
    display: flex;
    gap: 20px;
}

.item:hover{
    transform: scale(1.05);
    transition: 0.6s
}

@media (max-width: 1280px) {
    .item{
        width: 220px;
        font-size: 14px;
    }
    .item img{
        width: 220px;
    }
    .item-price{
        font-size: 14px;
    }
}

@media (max-width: 1024px) {
    .item{
        width: 170px;
        font-size: 13px;
    }
    .item img{
        width: 170px;
    }
    .item-price{
        font-size: 13px;
    }
}

@media (max-width: 800px) {
    .item{
        width: 120px;
        font-size: 12px;
    }
    .item img{
        width: 120px;
    }
    .item-price{
        font-size: 12px;
    }
}

@media (max-width: 500px) {
    .item{
        width: 160px;
    }
    .item img{
        width: 160px;
    }
}  